<script setup lang="ts">
import { useStore } from 'vuex';
import { computed, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import { SliderCards } from '@/features/slider-cards';

const store = useStore();
const route = useRoute();

onMounted(() => {
  store.dispatch('products/fetchProducts');
});

const products = computed(() => {
  const allProducts = store.state.products.productsData;


  const segments = route.path.split('/').filter(Boolean);
  const lastSegment = segments[segments.length - 1];


  if (lastSegment === 'product') {
    return allProducts;
  }

  return allProducts.filter((p: any) =>
    p.categories?.some((c: any) => c.slug === lastSegment)
  );
});
</script>

<template>
  <div class="product-list">
    <SliderCards v-for="product in products" :key="product.id" :cardData="product" />
  </div>
</template>

<style lang="scss" scoped>
.product-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;

}
</style>
